import React from 'react'
import { Route, Switch, BrowserRouter } from "react-router-dom";
import AOS from 'aos';
import 'aos/dist/aos.css';
// Custom
import Index from "views/home";
import Big_Havi_Info from 'views/home/Big_Havi';
import Lil_Darius_Info from 'views/home/Lil_Darius';
import Lil_Durk_Info from 'views/home/Lil_Durk';



export default function Routes() {
    React.useEffect(() => {
        AOS.init();
    });
    return (
        <>
            <BrowserRouter>
                <Switch>
                    <Route exact path="/" render={(props) => <Index {...props} />} />
                    <Route exact path="/artist/big_havi" render={(props) => <Big_Havi_Info {...props} />} />
                    <Route exact path="/artist/lil_darius" render={(props) => <Lil_Darius_Info {...props} />} />
                    <Route exact path="/artist/lil_durk" render={(props) => <Lil_Durk_Info {...props} />} />
                </Switch>
            </BrowserRouter>
        </>
    )
}